body {
  box-sizing: border-box;
  margin: 0px;
}
//Login/Register
.logWrapper,
.regWrapper,
.logoutWrapper {
  box-sizing: border-box;
  background: radial-gradient(
    circle,
    rgb(124, 69, 219) 30%,
    rgb(75, 40, 168) 66%,
    rgb(36, 18, 87) 100%
  );
  width: 100vw;
  margin: 0px;
}

.logWrapper {
  height: 100vh;
  padding-top: calc((100vh - 378px - 100px) / 2);
}
.regWrapper {
  height: 100vh;
  padding-top: calc((100vh - 520px) / 2);
}

.loginContainer {
  padding: 0px;
  width: inherit;
}
.registerContainer {
  padding: 0px;

  width: inherit;
}

.loginContainer,
.registerContainer {
  .logoWrapper {
    margin: 0px auto;
    color: white;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
      width: 30px;
      height: 30px;
      margin: 0px auto;
    }
  }

  .formLogReg {
    margin: 20px auto 0px auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;

    .formInput {
      display: flex;
      align-items: center;
      color: rgba(206, 204, 204, 0.664);
      font-size: 20px;
      font-weight: 400;
      border-bottom: 1px solid hsla(0, 2%, 80%, 0.664);
      padding: 0px 8px;
      max-width: 280px;

      svg {
        min-height: 40px;
        min-width: 20px;
      }

      input {
        background-color: inherit;
        margin-left: 3px;
        font-size: 20px;
        color: white;
        padding: 5px 10px;
        border: none;
        width: calc(100vw - 120px);
        padding-bottom: 2px;
        outline: none;
      }

      input:placeholder-shown {
        color: rgba(206, 204, 204, 0.664);
        font-weight: 400;
        font-size: 20px;
      }
    }

    .bigButton {
      display: block;
      text-align: center;
      padding: 15px 0px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 400;
      width: 300px;
      border-radius: 30px;
      border: 0px;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }

    .selectedBtn {
      background: -webkit-linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      color: white;
      border: none;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }

    .notSelectedBtn {
      color: rgb(92, 38, 187);
      background: none;
      background-color: white;
      font-size: 20px;
      font-weight: 400;
      border: none;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }
  }
}

//Shared Layout

.sharedLayoutWrapper {
  width: 100%;

  .sharedLayoutHeader {
        position: sticky;
          top: 0px;
    box-sizing: border-box;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    background-color: rgb(29, 11, 73);
    justify-content: space-between;
    border-bottom: 2px solid rgb(25, 2, 46);
    width: inherit;

    .logoWrapper {
      margin: 0px 0px;
      color: white;
      font-size: 14px;

      img {
        margin: 0px auto;
        width: 20px;
        height: 20px;
      }
    }

    .sharedLayoutUser {
      font-size: 18px;
      font-weight: 400;
      color: white;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      p {
        margin-right: 10px;
      }

      a {
        font-size: 24px;
        padding-top: 6px;
        border: none;
        outline: none;
        background-color: inherit;
        color: rgba(206, 204, 204, 0.664);

        &:hover {
          color: white;
          border-radius: 10px;
        }
      }
    }
  }
}

//Home page

.navLinks {
  display: flex;
  gap: 40px;
  justify-content: center;
  align-items: center;
  margin: 0px auto;
  padding: 30px 0px;

  a {
    padding: 6px;
    font-size: 38px;
    height: 44px;
    color: rgb(51, 25, 124);
    background-color: rgba(236, 231, 231, 0.664);
    border-radius: 10px;

    &:hover {
      box-shadow: 2px 2px 2px rgb(122, 79, 240);
    }
  }

  .selectedNav {
    color: white;
    background-color: rgb(122, 79, 240);

    &:hover {
      box-shadow: none;
    }
  }
}

.homePageWrapper {
  width: 100%;
  height: 100%;

  background: radial-gradient(
    circle,
    rgb(124, 69, 219) 30%,
    rgb(75, 40, 168) 66%,
    rgb(36, 18, 87) 100%
  );
}

.homePage {
  margin: 0px auto;
  max-width: 320px;

  .listCards {
    width: 300px;
    margin-bottom: 0px;
    margin: 0 auto;

    .totalSum {
      box-sizing: border-box;
      background-color: rgba(93, 38, 187);
      width: 290px;
      border: 1px solid rgba(56, 4, 104, 0.678);
      margin: 0 auto 15px auto;
      padding: 10px 25px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      box-shadow: 2px 2px 3px rgb(66, 3, 124);
      font-size: 25px;
      font-weight: 100;
      color: white;
    }

    .cardwithColor {
      width: 285px;
      border-radius: 10px;
      margin: 0px auto 15px auto;

      .card {
        background-color: rgb(92, 38, 187);
        width: 280px;
        border: 1px solid rgba(58, 28, 143, 1);
        border-bottom-right-radius: 10px;
        border-top-right-radius: 10px;
        margin-left: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-shadow: 2px 2px 3px rgb(66, 3, 124);

        .cardRow {
          border-bottom: 1px solid rgba(206, 204, 204, 0.664);
          font-size: 16px;
          color: white;
          font-weight: 600;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          padding: 0px 15px;
          align-items: center;
          min-height: 50px;
          gap:40px;
          .cardRowValue{max-width: 170px; text-align:justify;}
        }

        .cardRowButtons {
          height: 50px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0px 10px;

          button {
            border-radius: 20px;
            height: 35px;
            width: 80px;
          }
          .selectedBtn {
            background: -webkit-linear-gradient(
              to right,
              #f3a73c,
              hwb(288 30% 11%)
            );
            background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
            color: white;
            border: none;

            &:hover {
              box-shadow: 2px 2px 3px rgb(66, 3, 124);
            }
          }

          .notSelectedBtn {
            color: rgb(92, 38, 187);
            background: none;
            background-color: white;
            font-size: 20px;
            font-weight: 400;
            border: none;

            &:hover {
              box-shadow: 2px 2px 3px rgb(66, 3, 124);
            }
          }
        }
      }
    }

    .addCardButton {
      position: sticky;
      left: calc(100vw - 60px);
      bottom: 10px;
      font-size: 30px;
      font-weight: 900;
      border-radius: 40%;
      text-align: center;
      padding: 8px 8px 0px 8px;
      background: -webkit-linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      color: white;
      border: none;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }
  }
}

