//newCard
.newCardWrapper,
.logoutWrapper {
  box-sizing: border-box;
  width: 100vw;
  height: calc(100vh - 64px);

  background: radial-gradient(
    circle,
    rgb(124, 69, 219) 30%,
    rgb(75, 40, 168) 66%,
    rgb(36, 18, 87) 100%
  );
}

.newCardWrapper {
  padding-top: calc((100vh - 516px - 100px) / 2);
}
.newCardContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 18px;
  color: white;

  h2 {
    font-size: 24px;
  }

  .newCardToggle {
    display: flex;
    justify-content: space-between;
    width: 280px;
    margin: 0px auto;

    span {
      font-size: 22px;
      font-weight: 400;
    }

    .switch {
      position: relative;
      display: inline-block;
      width: 80px;
      height: 38px;

      input {
        opacity: 0;
        width: 0;
        height: 0;
      }

      .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: white;
        transition: 0.4s;
        border-radius: 34px;
      }

      .slider:before {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        content: '+';
        padding-top: 2px;
        font-size: 38px;
        font-weight: bold;
        color: white;
        height: 45px;
        width: 45px;
        left: -10px;
        bottom: -5px;
        background-color: rgb(119, 235, 65);
        border-radius: 50%;
        transition: 0.4s;
      }

      input:checked + .slider:before {
        content: 'âˆ’';
        font-size: 38px;
        color: white;
        background-color: rgb(211, 76, 76);
        transform: translateX(50px);
      }
    }
  }

  .newCardForm {
    background-color: inherit;
    outline: none;
    color: rgba(228, 223, 223, 0.664);
    padding: 0px 0px;
    width: 300px;
    margin: 0px auto;

    .rowFormNewCard {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      color: inherit;
      margin-top: 30px;
      width: 300px;
      border-bottom: 2px solid rgba(206, 204, 204, 0.664);
    }

    .categorySelection input {
      text-align: start;
      font-size: 22px;
      font-weight: 400;
      color: white;
      background-color: inherit;
      border: none;
      outline: none;
      max-width: 200px;
    }
    
    .categorySelection button {
      font-size: 25px;
      color: inherit;
      background-color: inherit;
      border: none;
      padding: 0px;
    }

    .newCardList {
      position: absolute;
      z-index: 1;
      width: 300px;
      list-style-type: none;
      background: linear-gradient(
        140deg,
        rgb(247, 201, 141) 10%,
        rgb(151, 85, 204) 45%,
        rgb(123, 20, 192) 80%
      );
      border: 1px solid rgba(206, 204, 204, 0.664);

      .newCardOption {
        padding: 5px 15px 5px 15px;
        text-align: center;

        input {
          background-color: inherit;
          border: none;
          outline: none;
          color: white;
          font-size: 20px;
          text-align: center;
          cursor: pointer;
        }
      }

      .newCardOption.income:hover {
        color: rgb(207, 47, 47);
        background-color: rgb(160, 116, 236);
      }

      .newCardOption.expense:hover {
        color: rgb(119, 235, 65);
        background-color: rgb(160, 116, 236);
      }
    }

    .amountAndDateSelection {
      display: flex;
      flex-direction: column;
      border-bottom: none;

      input:placeholder-shown {
        padding-left: 15px;
      }

      .amount {
        box-sizing: border-box;
        background-color: inherit;
        outline: none;
        border: none;
        width: 300px;
        font-size: 22px;
        border-bottom: 2px solid rgba(206, 204, 204, 0.664);
        color: white;
        padding-left: 15px;
      }

      .amount:placeholder-shown {
        box-sizing: border-box;
        color: rgb(224, 221, 221);
      }

      .date {
        box-sizing: border-box;
        background-color: inherit;
        outline: none;
        border: none;
        outline: none;
        font-size: 22px;
        width: 300px;
        margin: 25px 0px 0px 0px;
        text-align: start;
        color: white;
        border-bottom: 2px solid rgba(165, 163, 163, 0.664);
        padding: 0px 10px 0px 13px;
      }

      .date::-webkit-calendar-picker-indicator {
        filter: invert(1);
        font-size: 30px;
      }
    }

    .comment {
      border: none;
      outline: none;
      background-color: inherit;
      text-align: start;
      font-size: 20px;
      padding-left: 15px;
      color: white;
      width: 300px;
    }

    .comment:placeholder-shown {
      color: rgb(224, 221, 221);
      font-size: 20px;
      padding-left: 15px;
    }
    .newCardButtons {
      padding-top: 25px;
      padding-left: 1px;
      .bigButton {
        display: block;
        text-align: center;
        text-decoration: none;
        font-size: 20px;
        font-weight: 400;
        width: 300px;
        border-radius: 30px;
        border: 0px;

        &:hover {
          box-shadow: 2px 2px 3px rgb(66, 3, 124);
        }
      }

      .selectedBtn {
                padding: 18.5px 0px;
        background: -webkit-linear-gradient(
          to right,
          #f3a73c,
          hwb(288 30% 11%)
        );
        
        background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
        color: white;
        border: none;
        margin-bottom: 20px;
        &:hover {
          box-shadow: 2px 2px 3px rgb(66, 3, 124);
        }
      }

      .notSelectedBtn {
        padding: 15px 0px;
        color: rgb(92, 38, 187);
        background: none;
        background-color: white;
        font-size: 20px;
        font-weight: 400;
        border: none;

        &:hover {
          box-shadow: 2px 2px 3px rgb(66, 3, 124);
        }
      }
    }
  }
}

@media screen and (min-width: 480px) {
  .loginContainer,
  .registerContainer {
    .form {
      .formInput svg {
        min-width: 40px;
        min-height: 40px;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .logWrapper,
  .regWrapper,
  .homePageWrapper,
  .newCardWrapper,
  .logoutWrapper {
    background-image: url('../helpers//coins.png'),
      url('../helpers/SimpleBackground.webp');
    background-repeat: no-repeat, no-repeat;
    background-position: center, center;
    background-size: cover, cover;
    background-attachment: fixed;
  }

  .logWrapper {
    height: 100vh;
    padding-top: calc((100vh - 378px) / 2);
  }

  .regWrapper {
    height: 100vh;
    padding-top: calc((100vh - 520px) / 2);
  }
  .logoutWrapper {
    padding-top: calc((100vh - 260px - 64px) / 2);
  }
  .newCardWrapper {
      padding-top: calc((100vh - 516px - 64px) / 2);
    }

  .loginContainer,
  .registerContainer,
  .newCardContainer,
  .logoutContainer {
    padding: 10px 0px 15px 0px;
    border-radius: 20px;
    width: 450px;
    margin: 0px auto;
    background-color: rgb(101, 41, 204);
    background-color: radial-gradient(
      circle,
      rgb(124, 69, 219) 40%,
      rgb(75, 40, 168) 76%,
      rgb(36, 18, 87) 100%
    );
  }
}
